// pages/enterCommunity/enterCommunity.js
const context = require("../../context/Java110Context.js");
import pickerAddress from "../../components/pickerAddress/pickerAddress.vue"
const constant = context.constant;

export default {
	data() {
		return {
			areaCode: '',
			communityName: '',
			appUserName: '',
			idCard: '',
			link: '',
			msgCode: '',
			areaShow: false,
			areaList: {
				province_list: {},
				city_list: {},
				county_list: {}
			},
			province_list: {},
			city_list: {},
			county_list: {},
			multiArray: [
				['安徽', '湖北'],
				['合肥', '淮南', '六安'],
				['蜀山', '高新']
			],
			areaName:null
		};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		let _that = this;

		let _location = context.getLocation();

		let _currentLocation = context.getCurrentLocation();
		console.log(_currentLocation+"    444444");
		let _areaName = _currentLocation.city + _currentLocation.district;
		// console.log(_areaName);
		// let _areaCode = _currentLocation.adcode; //加载省份

		context._loadArea('', '', function(_areaList) {
			_that.areaList = _areaList,
				console.log("地址列表", _areaList);
			_that.communityName = _location
				// _that.areaCode = _areaCode,
				_that.areaName = _areaName
			// _that.setData({
			//   areaList: _areaList,
			//   communityName: _location,
			//   areaCode: _areaCode,
			//   areaName: _areaName
			// });
		});
	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		if (this.areaCode == '' || this.areaCode == undefined) {
			let _currentLocation = context.getCurrentLocation();

			let _areaName = _currentLocation.city + _currentLocation.district;

			let _areaCode = _currentLocation.adcode;
			this.areaCode = _areaCode;
			//this.areaName = _areaName;
			// this.setData({
			//   areaCode: _areaCode,
			//   areaName: _areaName
			// });
		}
	},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {},
	methods: {
		change:function(e){
			console.log(e);
			let city ='';
			for(let c in e.data){
				console.log(e.data[c]);
				city +=e.data[c].name;
			}
			this.$data.areaName = city;
		},
		bindInput: function(e) {
			console.log('数据监听', e);

			let _that = this;

			let dataset = e.currentTarget.dataset;
			let value = e.detail.value;
			let name = dataset.name;
			_that[name] = value; //  

			console.log(this);
		},
		sendMsgCode: function() {},
		bindOwner: function(e) {
			let obj = {
				"areaCode": this.areaCode,
				"communityName": this.communityName,
				"appUserName": this.appUserName,
				"idCard": this.idCard,
				"link": this.link,
				"msgCode": this.msgCode
			};
			let msg = "";

			if (obj.areaCode == "") {
				msg = "请选择地区";
			} else if (obj.communityName == "") {
				msg = "请填写小区名称";
			} else if (obj.appUserName == "") {
				msg = "请填写业主名称";
			} else if (obj.idCard == "") {
				msg = "请填写身份证";
			} else if (obj.link == "") {
				msg = "请填写手机号";
			} else if (obj.msgCode == "") {
				msg = "请填写验证码";
			}

			if (msg != "") {
				wx.showToast({
					title: msg,
					icon: 'none',
					duration: 2000
				});
			} else {
				console.log("提交数据", obj);
				wx.showLoading({
					title: '绑定中'
				});
				context.request({
					url: constant.url.appUserBindingOwner,
					header: context.getHeaders(),
					method: "POST",
					data: obj,
					//动态数据
					success: function(res) {
						console.log(res); //成功情况下跳转

						if (res.statusCode == 200) {
							wx.hideLoading();
							wx.redirectTo({
								url: "/pages/viewBindOwner/viewBindOwner"
							});
							return;
						}

						wx.hideLoading();
						wx.showToast({
							title: res.data,
							icon: 'none',
							duration: 2000
						});
					},
					fail: function(e) {
						wx.hideLoading();
						wx.showToast({
							title: "服务器异常了",
							icon: 'none',
							duration: 2000
						});
					}
				});
			}
		},
		onConfirm: function(e) {
			console.log("onConfirm", e);
			let _areaCode = e.detail.values[2].code;

			let _areaName = e.detail.values[1].name + e.detail.values[2].name;
			this.areaCode = _areaCode,
				this.areaName = _areaName,
				this.areaShow = false

			// this.setData({
			//   areaCode: _areaCode,
			//   areaName: _areaName,
			//   areaShow: false
			// });
		},
		onChange: function(e) {
			console.log(e);
		},
		onCancel: function(e) {
			this.areaShow = false;
			// this.setData({
			//   areaShow = false
			// });
		},

		/**
		 * 选择小区
		 */
		chooseCommunity: function(e) {
			if (this.areaCode == '' || this.areaCode == undefined) {
				wx.showToast({
					title: '请先选择地区',
					icon: 'none'
				});
				return;
			}

			wx.navigateTo({
				url: '/pages/viewCommunitys/viewCommunitys?areaCode=' + this.areaCode
			});
		}
	}
};
